// -------------------------------------------
// Custom HTMLElement Definitions
// -------------------------------------------

/** @constructor */
function ArmFooter () {
  const superCtor = (typeof Reflect === 'object') ? function () {
    return Reflect.construct(HTMLElement, arguments, this.constructor);
  } : function () {
    return HTMLElement.apply(this, arguments) || this;
  };
  const $_ = superCtor.apply(this, arguments);

  return $_;
}

ArmFooter.prototype = Object.create(HTMLElement.prototype);
ArmFooter.prototype.constructor = ArmFooter;
Object.setPrototypeOf(ArmFooter, HTMLElement);
Object.defineProperties(ArmFooter, {
  observedAttributes: {
    get: () => ([])
  }
});

// -------------------------------------------
// Private
// -------------------------------------------

const int = Math.trunc;
const print = s => console.log(`[Nothing] ${s}`);


// -------------------------------------------
// Custom HTMLElement (rest of the definitions)
// -------------------------------------------


function connectedCallback () {

  const root = this.attachShadow({ mode: 'open' });

  const cssCommonTemplate = require('!!raw-loader!css-minify!../ArmCommon/style.css');
  const styleElCommon = document.createElement("style")
  const cssCommon = cssCommonTemplate.default;
  if (!cssCommon) throw new Error('No style.');
  styleElCommon.innerText = cssCommon;
  root.appendChild(styleElCommon);

  const cssTemplate = require('!!raw-loader!css-minify!./footer.css');
  const styleEl = document.createElement("style")
  var css = cssTemplate.default;
  if (!css) throw new Error('No style.');
  styleEl.innerText = css;
  root.appendChild(styleEl);


  const htmlTemplate = require('raw-loader!html-minifier-loader!img-svg-inline-loader!../../templates/template-footer-' + process.env.build  +'.html');
  const templateEl = document.createElement('template');
  const text = htmlTemplate.default;
  if (!text) throw new Error('No template.');
  templateEl.innerHTML = text;
  root.appendChild(templateEl.content.cloneNode(true));

}

function disconnectedCallback () { }

function adoptedCallback () { }

function attributeChangedCallback (name, oldVal, newVal) { }

ArmFooter.prototype.connectedCallback = connectedCallback;
ArmFooter.prototype.disconnectedCallback = disconnectedCallback;
ArmFooter.prototype.adoptedCallback = adoptedCallback;
ArmFooter.prototype.attributeChangedCallback = attributeChangedCallback;

customElements.define('arm-footer-navigation', ArmFooter);

export default ArmFooter
