"use strict";

import SlideMenu from "./slidemenu";
import utilities from "./utilities";

var topnavigation = (function () {
  var _ACTIVE = "is-active";
  var _NAV = "c-top-navigation";
  var _SECTION_LINK = "c-top-navigation-section-link";
  var _TAB_LINK = "c-top-navigation-tab-link";
  var _BACKGROOUND_SOLID = "u-background-solid";
  var _OVERLAY = "c-top-navigation-overlay";
  var _WEBCOMPONENTNAME = "arm-top-navigation";
  var _TARGETSTOPGRDIENT = "ads-breadcrumbs"; //'#breadcrumb-element'; //'.c-header'
  var _SUBNAV_MOBILE_FULLSCREEN = "is-mobile-fullscreen";
  var _SUBNAV_MOBILE_FULLSCREEN_OFFSET = "is-mobile-fullscreen-offset";
  var _POSITION_STICKY_FIXED = "u-position-sticky-fixed";
  var _OPACITY_1 = "u-opacity-1";
  var intersection = null;

  /**
   * Constructor for topnavigation
   *
   * return {void}
   */
  function topnavigation(options) {
    var defaultOptions = {
      className: {
        root: _NAV,
        sectionLink: _SECTION_LINK,
        tabLink: _TAB_LINK,
      },
      overlayConfig: {
        attributes: true,
        childList: true,
        subtree: true,
      },
      paneConfig: {
        delay: 500,
      },
      observeId: _TARGETSTOPGRDIENT,
    };
    
    var config = Object.assign({}, defaultOptions, options);

    document.addEventListener("DOMContentLoaded", function () {
      var rootElement = document.querySelector("." + config.className.root) || document.querySelector(_WEBCOMPONENTNAME).shadowRoot; //.querySelector("." + config.className.root);
      var sectionLink = rootElement.querySelectorAll("." + config.className.sectionLink);
      var tabLink = rootElement.querySelectorAll("." + config.className.tabLink);
      for (var i = 0; i < sectionLink.length; i++) {
        submenuEventListener(rootElement.querySelector("." + config.className.root), sectionLink[i], config.observeId);
      }
      for (var i = 0; i < tabLink.length; i++) {
        submenuEventListener(rootElement.querySelector("." + config.className.root), tabLink[i], config.observeId);
      }

      navStickyObserver(document.querySelector(_WEBCOMPONENTNAME).shadowRoot.querySelector(".c-top-sub-navigation"), document.querySelector(_WEBCOMPONENTNAME).shadowRoot.querySelector(".c-top-navigation"), config);
      navGradientObserver(rootElement.querySelector("." + config.className.root), document.querySelector(config.observeId), config);
      navPaneObserver(rootElement.querySelector("." + config.className.root), config.paneConfig);
      navPaneObserver(rootElement.querySelector(".c-top-sub-navigation") !== null ? rootElement.querySelector(".c-top-sub-navigation").parentElement : null, config.paneConfig);

      navOverlayObserver(rootElement.querySelector("." + config.className.root), config.overlayConfig);
      navOverlayObserver(rootElement.querySelector(".c-top-sub-navigation"), config.overlayConfig);

      navDispatchEvent("arm-top-nav-search", rootElement.querySelector("#c-top-navigation-search"));
      navDispatchEvent("arm-top-sub-nav-search", rootElement.querySelector("#c-top-sub-navigation-search"));
      navDispatchEvent("arm-top-sub-nav-search", rootElement.querySelector("#c-top-sub-navigation-mobile-search"));

      rootElement.addEventListener("click", function (e) {
        if (e.target.classList.contains(_OVERLAY)) {
          var topNavRootElement = rootElement.querySelector(".c-top-navigation");
          removeStyleClassName(rootElement, _ACTIVE);
          setAriaAttributeValue(rootElement, "c-top-navigation-section-link", "aria-expanded", false);
          setAriaAttributeValue(rootElement, "c-top-navigation-section-tab", "aria-expanded", false);
          if (isInViewport(document.querySelector(config.observeId))) {
            topNavRootElement.classList.remove(_BACKGROOUND_SOLID);
          } else {
            topNavRootElement.classList.add(_BACKGROOUND_SOLID);
          }
        }
      });

      document.addEventListener("arm-top-nav-search", function (e) {
        location.href = "https://www.arm.com/search#q=" + e.detail;
      });
    });

    return config;
  }

  var throttle = function (f, delay) {
    let timer = 0;
    return function (...args) {
      clearTimeout(timer);
      timer = setTimeout(() => f.apply(this, args), delay);
    };
  };

  var isInViewport = function (elem) {
    if (elem === null) return false;

    var myElement = elem;
    var bounding = myElement.getBoundingClientRect();
    var myElementHeight = myElement.offsetHeight;
    var myElementWidth = myElement.offsetWidth;

    var bounding = myElement.getBoundingClientRect();

    return bounding.top >= -myElementHeight && bounding.left >= -myElementWidth && bounding.right <= (window.innerWidth || document.documentElement.clientWidth) + myElementWidth && bounding.bottom <= (window.innerHeight || document.documentElement.clientHeight) + myElementHeight;
  };

  function navStickyObserver(rootElement, target, config) {
    if (target === null || rootElement === null) return;
    function handleIntersection(entries) {
      entries.map((entry) => {
        intersection = entry.isIntersecting;
        if (entry.isIntersecting) {
          rootElement.classList.remove(_POSITION_STICKY_FIXED);
        } else {
          rootElement.classList.add(_POSITION_STICKY_FIXED);
        }
      });
    }
    const observer = new IntersectionObserver(handleIntersection);
    observer.observe(target);
  }

  function navGradientObserver(rootElement, target, config) {
    if (target === null) return;

    function handleIntersection(entries) {
      entries.map((entry) => {
        intersection = entry.isIntersecting;
        if (entry.isIntersecting) {
          rootElement.classList.remove(_BACKGROOUND_SOLID);
        } else {
          rootElement.classList.add(_BACKGROOUND_SOLID);
        }
      });
    }
    const observer = new IntersectionObserver(handleIntersection);
    observer.observe(target);
  }

  function navOverlayObserver(rootElement, config) {
    if (rootElement === null) return;

    var overlayObserver = new MutationObserver(function (mutations) {
      mutations.forEach(function (mutation) {
        if (mutation.target.localName === "li") {
          var rootEl = document.querySelector(_WEBCOMPONENTNAME) !== null ? document.querySelector(_WEBCOMPONENTNAME).shadowRoot : document;
          var overlays = rootEl.querySelectorAll("." + _OVERLAY);
          overlays.forEach(function (overlay) {
            overlay.remove();
            document.body.style.overflow = null;
          });
          if (mutation.target.querySelector("a").getAttribute("uirouter") !== null) return;
          if (mutation.target.classList.contains(_ACTIVE)) {
            var overlayEl = document.createElement("div");
            overlayEl.className = _OVERLAY;
            rootElement.parentNode.insertBefore(overlayEl, rootElement);
            document.body.style.setProperty("overflow", "hidden");
          }
        }
      });
    });

    overlayObserver.observe(rootElement, config);
  }

  function navPaneObserver(rootElement, config) {
    if (rootElement === null) return;
    let last = rootElement.getBoundingClientRect();
    const navResize = new ResizeObserver(
      throttle((entries) => {
        entries.forEach((entry) => {
          var currentWidth = last.width;
          var newWidth = entry.contentRect.width;
          var isActive = rootElement.querySelectorAll("." + _ACTIVE).length > 0;

          if (Math.abs(currentWidth - newWidth) < 50) return;

          if ((currentWidth < 1024 && newWidth > 1024 && isActive) || (currentWidth > 1024 && newWidth < 1024 && isActive)) {
            removeStyleClassName(rootElement, _ACTIVE);
            removeStyleClassName(rootElement, "is-mobile-fullscreen");
            console.log("remove", rootElement);
          }

          last.width = entry.contentRect.width;
        });
      }, config.delay)
    );

    navResize.observe(rootElement);
  }

  function navDispatchEvent(eventName, targetElement) {
    if (targetElement === null) return;

    targetElement.addEventListener(
      "keyup",
      utilities.debounce2(function (e) {
        if (e.keyCode === 13) {
          var event = new CustomEvent(eventName, {
            detail: targetElement.value,
          });
          document.dispatchEvent(event);
        }
      }, 500)
    );
  }

  /**
   * remove 'is-active'
   * @param {*} rootElement
   * @returns
   */
  function removeStyleClassName(rootElement, className) {
    var isActiveElementList = rootElement.querySelectorAll("." + className);
    isActiveElementList.forEach(function (item) {
      item.classList.remove(className);
    });
  }

  /**
   * Set aria attribute value(
   * @param {*} rootElement
   * @param {*} className
   * @param {*} ariaType
   * @param {*} value
   * @returns
   */
  function setAriaAttributeValue(rootElement, className, ariaType, value) {
    var elementList = rootElement.querySelectorAll("." + className);
    elementList.forEach(function (el) {
      el.setAttribute(ariaType, value);
    });
  }

  function resetSliderMenu(rootElement, clickElement) {
    if (rootElement === null) return;
    if (clickElement.parentElement.querySelector(".c-slide-menu") === null) return;

    var slideMenuComponent = rootElement.querySelector(".c-slide-menu")._slideMenu;
    slideMenuComponent !== undefined && slideMenuComponent.destroy();

    slideMenuComponent = new SlideMenu(rootElement.querySelector(".c-slide-menu"), {
      submenuLinkAfter: '<span class="c-slide-menu__LinkAfter arrow right"></span>',
      backLinkBefore: '<span class="c-slide-menu__LinkBefore arrow left"></span>',
    });
  }

  /**
   * click event for toggle submenu
   * @param {*} rootElement
   * @param {*} eventElement
   * @returns
   */
  function submenuEventListener(rootElement, eventElement, observeId) {
    function submenu(e) {
      e.preventDefault();
      // document.querySelector("nav").scrollIntoView();
      var root = document.querySelector(_WEBCOMPONENTNAME).shadowRoot;
      var isActive = this.parentElement.classList.contains(_ACTIVE);
      // var tabPanel = this.parentElement.querySelector(".submenu .c-top-navigation-tab-pane");

      //root.querySelector("nav").scrollIntoView();
      //tabPanel != null && tabPanel.scrollTo({ top: 0, behavior: "smooth" });

      removeStyleClassName(root, _ACTIVE);
      removeStyleClassName(root, _SUBNAV_MOBILE_FULLSCREEN);
      removeStyleClassName(root, _SUBNAV_MOBILE_FULLSCREEN_OFFSET);
      removeStyleClassName(root, _OPACITY_1);
      setAriaAttributeValue(rootElement, this.className, "aria-expanded", false);

      isActive && this.blur();
      !isActive && this.parentElement.classList.add(_ACTIVE);
      !isActive && this.parentElement.parentElement.classList.add(_ACTIVE);

      this.setAttribute("aria-expanded", !isActive);

      if (!isActive && this.parentElement.classList.contains("c-top-sub-navigation-mobile")) {
        var rootSubNav = root.querySelector(".c-top-sub-navigation");
        var rootSubNavPosition = rootSubNav.getBoundingClientRect();
        var marginTopOffset =  rootSubNav.classList.contains(_POSITION_STICKY_FIXED) ? _SUBNAV_MOBILE_FULLSCREEN : _SUBNAV_MOBILE_FULLSCREEN_OFFSET;
        rootSubNav.classList.add(marginTopOffset);
        setTimeout(function(){ 
          rootSubNav.querySelector('.c-top-sub-navigation-mobile.is-active .submenu').classList.add(_OPACITY_1); 
        }, 500);
       
      }
      
      if (isInViewport(document.querySelector(observeId))) {
        isActive ? rootElement.classList.remove(_BACKGROOUND_SOLID) : rootElement.classList.add(_BACKGROOUND_SOLID);
      } else {
        rootElement.classList.add(_BACKGROOUND_SOLID);
      }

      resetSliderMenu(document.querySelector(_WEBCOMPONENTNAME).shadowRoot.querySelector(".c-top-navigation"), this);
      resetSliderMenu(document.querySelector(_WEBCOMPONENTNAME).shadowRoot.querySelector(".c-top-sub-navigation"), this);
    }

    eventElement.addEventListener("click", submenu);
    eventElement.addEventListener("keypress", function (e) { e.keyCode === 13 && submenu.bind(this)(e); });
  }

  return topnavigation;
})();

export default topnavigation;
