import topnavigation from "./topNav";

// -------------------------------------------
// Custom HTMLElement Definitions
// -------------------------------------------

/** @constructor */
function ArmTopNavigation() {
  const superCtor =
    typeof Reflect === "object"
      ? function () {
          return Reflect.construct(HTMLElement, arguments, this.constructor);
        }
      : function () {
          return HTMLElement.apply(this, arguments) || this;
        };
  const $_ = superCtor.apply(this, arguments);
  $_.observeId = null;
  $_.topnav = null;
  return $_;
}

ArmTopNavigation.prototype = Object.create(HTMLElement.prototype);
ArmTopNavigation.prototype.constructor = ArmTopNavigation;
Object.setPrototypeOf(ArmTopNavigation, HTMLElement);
Object.defineProperties(ArmTopNavigation, {
  observedAttributes: {
    get: () => ["observeid"],
  },
});

// -------------------------------------------
// Private
// -------------------------------------------

const int = Math.trunc;
const print = (s) => console.log(`[Nothing] ${s}`);
const replaceUndefinedOrNull = (key, value) => {
  if (value === null || value === undefined) {
    return undefined;
  }

  return value;
};

const setObserveId = ($_) => (observeId = null) => {
    if ($_.nothing) {
      console.log('setObserveId')
    }
};
// -------------------------------------------
// Custom HTMLElement (rest of the definitions)
// -------------------------------------------

function connectedCallback() {
  const root = this.attachShadow({ mode: "open" });

  // Document.prototype.createElementFromString = function (str) {
  //   const element = new DOMParser().parseFromString(str, "text/html");
  //   const child = element.documentElement.querySelector("head").firstChild;
  //   return child;
  // };
  // var fontawesome = '<link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.7.1/css/all.css" integrity="sha384-fnmOCqbTlWIlj8LyTjo7mOUStjsKC4pOpQbqyi7RrhN7udi9RwhKkMHpvLbHG9Sr" crossorigin="anonymous">';
  // window.document.head.appendChild(document.createElementFromString(fontawesome));
  // var styles = document.querySelector('link[href*="fontawesome"]');
  // root.appendChild(styles.cloneNode());

  const cssCommonTemplate = require("!!raw-loader!css-minify!../ArmCommon/style.css");
  const styleElCommon = document.createElement("style");
  const cssCommon = cssCommonTemplate.default;
  if (!cssCommon) throw new Error("No style.");
  styleElCommon.innerText = cssCommon;
  root.appendChild(styleElCommon);

  const cssTemplate = require("!!raw-loader!css-minify!./topNav.css");
  const styleEl = document.createElement("style");
  const css = cssTemplate.default;
  if (!css) throw new Error("No style.");
  styleEl.innerText = css;
  root.appendChild(styleEl);

  if (this.getAttribute("subnav") !== null) {
    const cssTemplate2 = require("!!raw-loader!css-minify!./topSubNav.css");
    const styleEl2 = document.createElement("style");
    const css2 = cssTemplate2.default;
    if (!css2) throw new Error("No style.");
    styleEl2.innerText = css2;
    root.appendChild(styleEl2);
  }

  const htmlTemplate = require("raw-loader!html-minifier-loader!img-svg-inline-loader!../../templates/template-topnav-" + process.env.build + ".html");
  const templateEl = document.createElement("template");
  const text = htmlTemplate.default;
  if (!text) throw new Error("No template.");
  templateEl.innerHTML = text;
  root.appendChild(templateEl.content.cloneNode(true));

  const options = JSON.parse(JSON.stringify({ observeId: this.getAttribute("observeId") }, replaceUndefinedOrNull));
  new topnavigation(options);
  
}

function disconnectedCallback () { }

function adoptedCallback () { }

function attributeChangedCallback (name, oldVal, newVal) {
  if (name === 'observeid') {

  }
}

ArmTopNavigation.prototype.connectedCallback = connectedCallback;
ArmTopNavigation.prototype.disconnectedCallback = disconnectedCallback;
ArmTopNavigation.prototype.adoptedCallback = adoptedCallback;
ArmTopNavigation.prototype.attributeChangedCallback = attributeChangedCallback;

customElements.define("arm-top-navigation", ArmTopNavigation);

export default ArmTopNavigation;
